@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+JP:wght@400;700&display=swap);
* {
  box-sizing: border-box;
}

html {
  font-family: 'Noto Sans JP', sans-serif;
}

body {
  margin: 0;
  padding: 0;
  background-color: rgb(110, 66, 168);
}


.Block_container__3mnTP{
    width: 25%;
    margin-bottom: 10px;
    height: 100px;
    cursor: pointer;
    transition: all 1s ease-out;
}

.Block_displayDiv__1fJuy{
    width: 95%;
    margin: auto;
    height: 100px;
    border-radius: 5px;
    text-align: center;
    line-height: 100px;
    font-size: 50px;
}

.Block_shakeDisplayDiv__SoYBW{
    width: 100%;
    height: 100px;
    border-radius: 5px;
    text-align: center;
    line-height: 100px;
    font-size: 50px;
    -webkit-animation: Block_shake__XqOu6 1s ease-in;
            animation: Block_shake__XqOu6 1s ease-in;
}

@-webkit-keyframes Block_shake__XqOu6 {  
    0%{
        -webkit-transform: translate(0);
                transform: translate(0);
    }
    50%{
        -webkit-transform: translate(0);
                transform: translate(0);
    }
    60%,80%{
        -webkit-transform: translate(20px);
                transform: translate(20px);
        box-shadow: 0px 6px 20px rgba(0, 0, 0, 0.5);
    }
    70%,90%{
        -webkit-transform: translate(-20px);
                transform: translate(-20px);
    }
    100%{
        -webkit-transform: translate(0);
                transform: translate(0);
    } 
}

@keyframes Block_shake__XqOu6 {  
    0%{
        -webkit-transform: translate(0);
                transform: translate(0);
    }
    50%{
        -webkit-transform: translate(0);
                transform: translate(0);
    }
    60%,80%{
        -webkit-transform: translate(20px);
                transform: translate(20px);
        box-shadow: 0px 6px 20px rgba(0, 0, 0, 0.5);
    }
    70%,90%{
        -webkit-transform: translate(-20px);
                transform: translate(-20px);
    }
    100%{
        -webkit-transform: translate(0);
                transform: translate(0);
    } 
}

@media screen and (max-width: 850px) {
    .Block_container__3mnTP{
    margin-bottom: -12px;

    }
    .Block_displayDiv__1fJuy{
        width: 90%;
        height: 80px;
        line-height: 80px;
    }
    .Block_shakeDisplayDiv__SoYBW{
        width: 90%;
        height: 80px;
        line-height: 80px;
    }

    @-webkit-keyframes Block_shake__XqOu6 {  
        0%{
            -webkit-transform: translate(0);
                    transform: translate(0);
        }
        50%{
            -webkit-transform: translate(0);
                    transform: translate(0);
        }
        60%,80%{
            -webkit-transform: translate(10px);
                    transform: translate(10px);
            box-shadow: 0px 4px 15px rgba(0, 0, 0, 0.5);
        }
        70%,90%{
            -webkit-transform: translate(-10px);
                    transform: translate(-10px);
        }
        100%{
            -webkit-transform: translate(0);
                    transform: translate(0);
        } 
    }

    @keyframes Block_shake__XqOu6 {  
        0%{
            -webkit-transform: translate(0);
                    transform: translate(0);
        }
        50%{
            -webkit-transform: translate(0);
                    transform: translate(0);
        }
        60%,80%{
            -webkit-transform: translate(10px);
                    transform: translate(10px);
            box-shadow: 0px 4px 15px rgba(0, 0, 0, 0.5);
        }
        70%,90%{
            -webkit-transform: translate(-10px);
                    transform: translate(-10px);
        }
        100%{
            -webkit-transform: translate(0);
                    transform: translate(0);
        } 
    }

}
